.products {
  background-color: $hero-text-color;
}
.products__container {
  @extend %auto-margin;

  @media screen and (max-width: 768px) {
    width: 320px;
    padding: 121px 20px 98px 20px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 122px 0 112px 0;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
    padding: 127px 0 119px 0;
  }
}
.products__title {
  margin-bottom: 144px;
  text-align: center;
  font-family: $secondary-font;
  color: $tertiary-text-color;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  margin-left: auto;
  margin-right: auto;
  @media screen and (max-width: 768px) {
    font-size: 18px;
    line-height: 1.17;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 150px;
    font-size: 26px;
    line-height: 1.15;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 259px;
    font-size: 30px;
    line-height: 1.13;
    letter-spacing: 0.04em;
  }
}
.products__title--accent {
  display: inline-block;
  margin-top: 15px;
  color: $accent-color;
  text-align: center;
  line-height: 1.15;
  letter-spacing: 0.04em;
  @media screen and (max-width: 768px) {
    font-size: 34px;
  }
  @media screen and (min-width: 768px) {
    font-size: 48px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.14;
    letter-spacing: 0.06em;
  }
}
.products__title > a {
  margin: 0;
  padding: 0;
}
.products__list {
  @extend %list;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -20px;
  }
  @media screen and (min-width: 1200px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -30px;
  }
}
.products__item {
  position: relative;
  border-radius: 24px;
  @media screen and (max-width: 768px) {
    padding-top: 161px;
    padding-bottom: 41px;
    width: 280px;
    height: 485px;
  }
  &:not(:last-child) {
    @media screen and (max-width: 768px) {
      margin-bottom: 130px;
    }
  }
  @media screen and (min-width: 768px) {
    width: 220px;
    height: 485px;
    margin-left: 20px;
    padding-top: 152px;
    padding-bottom: 40px;
  }
  @media screen and (min-width: 1200px) {
    width: 329px;
    height: 525px;
    margin-left: 30px;
    padding-top: 163px;
    padding-bottom: 50px;
  }
}
.products__item--ice-cream {
  background-color: $hero-bgr-color;
}
.products__item--ice-coffee {
  background-color: $secondary-bgr-color;
}
.products__item--milkshakes {
  background-color: $tettiary-bgr-color;
}
.products__image {
  display: flex;
  justify-content: center;
  position: absolute;
  top: -113px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  margin: 0 auto;
  @media screen and (max-width: 768px) {
    min-width: 329px;
  }
  @media screen and (min-width: 768px) {
    top: -111px;
  }
  @media screen and (min-width: 1200px) {
    top: -210px;
    min-width: 335px;
  }
}

.products__item__title {
  margin-bottom: 30px;
  text-align: center;
  font-family: $secondary-font;
  color: $hero-text-color;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  @media screen and (min-width: 1200px) {
    font-size: 34px;
    line-height: 1.13;
  }
}
.products__link {
  text-decoration: none;
}
.points__list {
  display: flex;
  margin-left: -4px;
  justify-content: center;
  @extend %auto-margin;
  @extend %list;
}
.points__item {
  margin-left: 4px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: $hero-text-color;
}
.products__card {
  text-align: center;
  margin: 30px 0;
  height: 124px;
  overflow: hidden;
  @media screen and (max-width: 768px) {
    padding: 0 40px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
    padding: 0 11px;
  }
  @media screen and (min-width: 1200px) {
    margin: 32px 0 62px;
    padding: 0 44px;
    height: 106px;
  }
}
.products__card__text {
  font-family: $primary-font;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;
  letter-spacing: 0.04em;
  text-align: center;
  color: $hero-text-color;
  @media screen and (min-width: 768px) {
    line-height: 1.76;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.76;
  }
}
.products__icon {
  @extend %auto-margin;
}
